*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  margin: 0;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

main {
  overflow: hidden;
  position: relative;
  z-index: 1;
}

a {
  text-decoration: none;
}

ul,
ol,
nav {
  list-style: none;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

.container {
  max-width: 675px;
  width: 100%;
  padding: 0 24px;
  margin: 0 auto;
  box-sizing: border-box;
  position: relative;
}

.quiz {
  padding: 50px 0;
}
.quiz__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media only screen and (min-width: 768px) {
  .quiz__container {
    padding: 0 20px;
  }
}
.quiz__step {
  display: none;
}
.quiz__step.open {
  display: block;
}
.quiz__title, .quiz__result {
  transform: translateY(0);
  opacity: 1;
  transition: 0.3s ease;
}
.quiz__title.hidden, .quiz__result.hidden {
  transform: translateY(10px);
  opacity: 0;
}
.quiz__title {
  font-size: 24px;
  line-height: 1.4;
}
@media only screen and (min-width: 768px) {
  .quiz__title span {
    position: absolute;
    left: -22px;
  }
}
.quiz__subtitle {
  font-size: 20px;
  line-height: 1.4;
  font-style: italic;
  margin-top: 10px;
}
.quiz__type {
  font-weight: 700;
  font-size: 22px;
  line-height: 1.4;
  margin-top: 30px;
}
@media only screen and (min-width: 768px) {
  .quiz__type {
    font-size: 30px;
    margin-top: 50px;
  }
}
@media only screen and (min-width: 768px) {
  .quiz__type span {
    position: absolute;
    left: -8px;
  }
}
.quiz__fields {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 20px;
}
.quiz__error {
  width: 100%;
  color: red;
  font-size: 20px;
  margin-top: 10px;
}
.quiz__result {
  font-weight: 700;
  font-size: 40px;
  line-height: 1.4;
  text-align: center;
}
.quiz__next {
  padding: 8px 35px;
  background-color: #4aa3fc;
  color: #fff;
  border-radius: 5px;
  font-size: 20px;
  line-height: 1.5;
  margin-top: 30px;
  cursor: pointer;
  transition: 0.3s ease;
}
@media only screen and (min-width: 768px) {
  .quiz__next {
    margin-top: 50px;
  }
}
.quiz__next:hover {
  background-color: #2490fb;
  color: #fff;
}
.quiz.d-none,
.quiz .d-none {
  display: none;
}

.field {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #eee;
  border: 1px solid #bbb;
  border-radius: 5px;
  transition: 0.3s ease;
}
.field:hover {
  background-color: #c7e3ff;
}
.field.closed {
  display: none;
}
.field.active {
  background-color: #4aa3fc;
}
.field.active .field__label {
  color: #fff;
}
.field.active .field__input {
  display: block;
}
.field--checkbox {
  background-color: transparent;
  border: 0;
}
.field--checkbox:hover {
  background-color: transparent;
}
.field--checkbox:hover b {
  background-color: #c7e3ff;
}
.field--checkbox b {
  position: absolute;
  inset: 0;
  z-index: -1;
  background-color: #eee;
  border: 1px solid #bbb;
  border-radius: 5px;
  transition: 0.3s ease;
}
.field--checkbox .field__input:checked + span {
  color: #fff;
}
.field--checkbox .field__input:checked + span + b {
  background-color: #4aa3fc;
}
.field__label {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 15px;
  font-size: 19px;
  line-height: 1.2;
  cursor: pointer;
  transition: 0.3s ease;
}
.field__input {
  display: none;
  width: calc(100% - 30px);
  height: 52px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin: 5px 15px 15px;
  padding: 5px 15px;
  color: #333;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  resize: none;
  transition: border-color 0.3s ease;
}
.field__input:hover, .field__input:focus {
  border-color: #66afe9;
}
.field__input::placeholder {
  color: rgba(51, 51, 51, 0.5);
  font-size: 14px;
}